/*
 * School Fund Page Styles
 * Following exact Figma specifications with centered layout
 */

// Import design tokens
@import 'variables';

// Reset
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

// Base
body {
  font-family: $font-sans;
  font-size: $font-size-body;
  line-height: $lh-body;
  font-weight: $weight-regular;
  background-color: $color-bg-light;
  color: $color-body;
}

// Hero Section
.hero-section {
  position: relative;
  width: 100%;
  margin-bottom: 100px; // Space for overlay
}

.hero-image-container {
  width: 100%;
  height: 400px;
  overflow: hidden;
  
  .hero-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

// Hero Overlay - 831x206
.hero-overlay {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%) translateY(50%);
  width: 831px;
  height: 206px;
  background-color: $color-white;
  box-shadow: 0 4px 24px rgba(0, 0, 0, 0.1);
  display: flex;
  align-items: center;
  justify-content: center;
  
  @media (max-width: 900px) {
    width: 90%;
    max-width: 831px;
    height: auto;
    padding: $space-lg;
  }
}

.overlay-content {
  text-align: center;
  padding: $space-lg;
}

.breadcrumb {
  font-size: $font-size-body-sm;
  margin-bottom: $space-md;
  
  a {
    color: $color-link;
    text-decoration: none;
    
    &:hover {
      text-decoration: underline;
    }
  }
  
  .separator {
    margin: 0 $space-sm;
    color: $color-breadcrumb;
  }
  
  .current {
    color: $color-breadcrumb;
  }
}

.hero-title {
  font-family: $font-serif;
  font-size: $font-size-h1;
  line-height: $lh-h1;
  color: $color-heading;
  font-weight: $weight-regular;
  margin-bottom: $space-sm;
}

.item-count {
  font-size: $font-size-body-sm;
  color: $color-body;
}

// Page Container
.page-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

// Main Content
.main-content {
  width: 100%;
  max-width: 700px;
  margin: 0 auto;
  padding: 0 $space-md;
  display: flex;
  flex-direction: column;
  align-items: center;
}

// About Section
.about-section {
  width: 100%;
  margin-bottom: $space-xl;
  
  .section-title {
    font-family: $font-serif;
    font-size: $font-size-h2;
    line-height: $lh-h2;
    color: $color-heading;
    margin-bottom: $space-lg;
    font-weight: $weight-regular;
  }
}

.about-wrapper {
  display: grid;
  grid-template-columns: 1fr 300px;
  gap: $space-xl;
  align-items: start;
  
  @media (max-width: $breakpoint-md) {
    grid-template-columns: 1fr;
  }
}

.about-text {
  p {
    color: $color-body;
    font-size: $font-size-body-sm;
    line-height: $lh-body;
    margin-bottom: $space-md;
    
    &:last-child {
      margin-bottom: 0;
    }
  }
}

.about-image-frame {
  background-color: #B5A9A3;
  padding: $space-md;
  
  img {
    width: 100%;
    height: auto;
    display: block;
  }
}

// Search Section (Top)
.search-section {
  width: 100%;
  background-color: $color-search-bg;
  padding: $space-lg;
  margin-bottom: $space-xl;
  border-radius: 4px;
}

.search-title {
  font-family: $font-sans;
  font-size: $font-size-body-sm;
  font-weight: $weight-semibold;
  color: $color-heading;
  margin-bottom: $space-md;
  letter-spacing: 0.5px;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: $space-sm;
}

.search-label {
  font-size: $font-size-input-label;
  color: $color-body;
}

.search-inputs {
  display: flex;
  gap: $space-md;
}

.search-field {
  flex: 1;
  padding: 10px $space-md;
  border: 1px solid $color-input-border;
  border-radius: 4px;
  font-size: $font-size-body-sm;
  background-color: $color-white;
  
  &:focus {
    outline: none;
    border-color: $color-link;
  }
}

.search-submit {
  padding: 10px $space-lg;
  background-color: $color-primary;
  color: $color-white;
  border: none;
  border-radius: 4px;
  font-size: $font-size-body-sm;
  font-weight: $weight-semibold;
  cursor: pointer;
  transition: background-color 0.3s;
  
  &:hover {
    background-color: $btn-primary-hover;
  }
}

// School Fund Highlights
.highlights-section {
  width: 100%;
  margin-bottom: $space-xl;
}

.school-grid {
  display: grid;
  gap: $space-lg;
  margin-bottom: $space-xl;
}

.school-card {
  display: flex;
  gap: $space-lg;
  align-items: flex-start;
  
  @media (max-width: $breakpoint-sm) {
    flex-direction: column;
  }
}

.school-image {
  flex: 0 0 200px;
  height: 150px;
  overflow: hidden;
  
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  @media (max-width: $breakpoint-sm) {
    flex: 1;
    width: 100%;
  }
}

.school-info {
  flex: 1;
  
  .school-name {
    font-family: $font-serif;
    font-size: $font-size-label-sm;
    line-height: 1.3;
    color: $color-link;
    margin-bottom: $space-sm;
    font-weight: $weight-regular;
  }
  
  .school-description {
    color: $color-body;
    font-size: $font-size-body-sm;
    line-height: $lh-body;
  }
}

// Browse Button (In Highlights)
.browse-button-container {
  text-align: center;
  padding: $space-lg 0;
}

.browse-button {
  display: inline-flex;
  align-items: center;
  gap: $space-sm;
  padding: 12px 24px;
  background-color: $color-primary;
  color: $color-white;
  text-decoration: none;
  border-radius: 4px;
  font-size: $font-size-body-sm;
  font-weight: $weight-semibold;
  transition: background-color 0.3s;
  
  &:hover {
    background-color: $btn-primary-hover;
  }
  
  .arrow {
    font-size: 1.2em;
  }
}